html {
	box-sizing: border-box;
	overflow-x: hidden;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	margin: 0;
	font-family: "Roboto", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
		"Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;

	text-transform: uppercase;

	font-size: 2vh;
	line-height: 3.5vh;
	letter-spacing: 0.2em;
}

img {
	display: block;
	overflow: hidden;
	height: auto;
}
p {
	margin: 0;
	padding: 0;
}
h1,
h2,
h3 {
	margin: 0;
	padding: 0;
}
a {
	text-decoration: none;
}
body,
html {
	max-height: 100vh;
}
.list-style {
	margin: 0;
	padding: 0;
	list-style: none;
}
.container {
	margin-right: auto;
	margin-left: auto;
	min-height: 100vh;
}
.social-item__ico {
	fill: #dfdfdf;
	-webkit-filter: drop-shadow(0px 0px 5px #dfdfdfb4);
	        filter: drop-shadow(0px 0px 5px #dfdfdfb4);
	cursor: pointer;
	transition: fill 0.3s linear;
}

.social-item__ico:hover,
.social-item__ico:focus {
	fill: #80dfd2c9;
}

.social-item__ico {
	fill: #dfdfdf;
}
button {
	display: flex;
	justify-items: center;
	align-items: center;
}

.header {
	position: fixed;
	min-width: 100%;
	z-index: 10;
	padding: 1vw 2vw;
	opacity: 0;
	-webkit-animation: header-footer 2s linear 0.5s forwards;
	        animation: header-footer 2s linear 0.5s forwards;
}

@-webkit-keyframes header-footer {
	0% {
		opacity: 0.1;
	}
	33% {
		opacity: 0.6;
	}

	66% {
		opacity: 0.9;
	}

	100% {
		opacity: 1;
	}
}

@keyframes header-footer {
	0% {
		opacity: 0.1;
	}
	33% {
		opacity: 0.6;
	}

	66% {
		opacity: 0.9;
	}

	100% {
		opacity: 1;
	}
}
.header-nav {
	min-width: 100%;
	margin-left: auto;
	margin-right: 10vw;
	font-family: "Roboto";
	display: flex;
}
.logo {
	font-family: "Monoton";
	padding: 0;
	margin: 0;
	font-size: 1.7vw;
	color: #dfdfdf;
	line-height: 100%;
	letter-spacing: 0.2em;
	text-shadow: 4px 4px 10px rgba(0, 0, 0, 0.623);
}

.header-nav__list {
	display: flex;
	flex-wrap: nowrap;
	margin-left: auto;
}

.header-nav__item {
	text-transform: uppercase;
	font-weight: 400;
	color: #969696;
	font-size: 0.8vw;
	cursor: pointer;
	transition: color 100ms linear;
}

.header-nav__anchor {
	color: #b3acac;
	padding: 10px;
}

.header-nav__anchor:focus,
.header-nav__anchor:hover {
	color: #80dfd2c9;
}

.active {
	position: relative;
	font-weight: 900;
	color: #80dfd2c9;
}

@media screen and (min-width: 320px) and (max-width: 739px) {
	.header {
		padding: 10px;
	}

	.header-nav {
		align-items: center;
	}
	.header-nav__list {
		margin-left: auto;
		margin-right: auto;
	}

	.logo {
		display: none;
	}

	.header-nav__anchor {
		font-size: 8px;
		letter-spacing: 0mm;
	}
}

